
import XHeader from 'vux/src/components/x-header/index.vue'

import {
types
} from 'util';
export default {
name: 'FileLogin',
components: {
XHeader
},
data() {
return {
PWD: '',
PWD1: '',
PWD2: '',
PWDtest: true,
PWD1test: true,
PWD2test: true,
disable: true,
}
},
mounted() {},
methods: {
hideerro() {
this.PWDtest = true;
},
testpwd(str) {
var regUpper = /[A-Z]/;
var regLower = /[a-z]/;
var regStr = /[0-9]/;
var complex = 0;
if (regLower.test(str)) {
++complex;
}
if (regUpper.test(str)) {
++complex;
}
if (regStr.test(str)) {
++complex;
}
if (complex < 3 || str.length < 6) {
return false;
} else {
return true;
}
},
changetext() {
this.PWDtest = this.testpwd(this.PWD) ? true : false;
this.PWD1test = this.testpwd(this.PWD1) ? true : false;
this.PWD2test = (this.PWD2 != this.PWD1 && this.PWD1 != '') ? false : true;
if (this.PWD2test && this.PWD1test && this.PWD.length >= 6 && this.PWD1.length >= 6 && this.PWD2.length >= 6) {
this.disable = false;
} else {
this.disable = true;
}
},
NextStep() {
if (this.disable) {
return false
}
this.$ajax.post('', {
'MSH.1': 'user',
'MSH.2': 'updatePassword',
'tel': this.$root.loginInfo.CHZSJH,
'password': this.PWD1,
'oldpassword': this.PWD,
'token':this.$root.loginInfo.token,
}).then(data => {
if (typeof data != 'undefined') {
if (data.status == 0) {
let that = this;
this.$vux.confirm.show({
title: '您还未注册',
confirmText: '确定',
showCancelButton: false,
onConfirm() {
if (that.$root.phoneType == 'ios') {
window.webkit.messageHandlers.mutual.postMessage({
type: "1"
});
} else {
android: AndroidFun.mutual('1');
}
}
});
} else if (data.status == 1) {
this.$root.loginInfo.isLogin = false;
this.$router.isBack = true;
this.$router.push('/mine');
let that = this;
this.$vux.toast.show({
text: '修改成功，请重新登录',
type: 'success',
onHide(){
if (that.$root.phoneType == 'ios') {
window.webkit.messageHandlers.mutual.postMessage({
type: "1"
});
} else {
android: AndroidFun.mutual('1');
}
}
});
} else if (data.status == 2) {
this.PWDtest = false;
this.disable = true;
this.$vux.toast.text("旧密码错误", "bottom");
}
}
}).catch(error => {
console.log(error);
});
},
},
watch: {
PWD(val) {
this.changetext()
},
PWD1(val) {
this.changetext()
},
PWD2(val) {
this.changetext()
}
}
}



